@media screen and (max-width:1005px) {
  

    .bussines__item {
        display: flex;
        align-items: center;
        justify-content: space-between;
    }

    .bussines__item img {
        width: 350px;
        height: 350px;
    }

    .item__text {
        padding-left: 0px;
    }
}

@media screen and (max-width: 810px) {
    .bussines__item {
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
    }
    .bussines__subtitle{
        padding-bottom: 40px;
    }

    .item__text p {
        max-width: 600px;
        text-align: center;
    }
    .bussines__item{
        margin: 0;
        padding: 0;
    }
    .bussines__item img {
        display: block;
        width: 350px;
        height: 300px;
    }

}
@media screen and (max-width:636px){
    .bussines {
        padding: 90px 0 50px 0;
    }
    .item__text h4{
        /* display: inline-block; */
        text-align: center;
        color: var(--main-text, #101010);
        font-size: 30px;
        font-family: Plus Jakarta Sans;
        font-style: normal;
        font-weight: 700;
        line-height: 140%;
        padding-bottom: 20px;
    }
    .bussines__item:nth-child(1){
        padding-bottom: 0px;
        gap: 0px;
    }
    .bussines__item:nth-child(2){
        padding-bottom: 0px;
        gap: 0px;
    }
    .bussines__item:nth-child(3){
        gap: 0px;
    }
    .bussines__title {
        color: #101010;
        text-align: center;
        font-size: 28px;
        font-family: Plus Jakarta Sans;
        font-style: normal;
        font-weight: 600;
        line-height: 100%;
        padding-bottom: 16px;
    }
    .bussines__subtitle {
        display: inline-block;
        max-width: 400px;
        color: #636F87;
        text-align: center;
        font-size: 15px;
        font-family: Plus Jakarta Sans;
        font-style: normal;
        font-weight: 400;
        line-height: 150%;
        padding-bottom: 50px;
    }
    .bussines__item img {
        width: 350px;
        height: 300px;
    }
    .bussines__item:nth-child(1){
        padding-bottom: 40px;
    }
    .bussines__item:nth-child(2){
        padding-bottom: 40px;
    }
    .item__text h4 {
        display: block;
        color: var(--main-text, #101010);
        font-size: 20px;
        font-family: Plus Jakarta Sans;
        font-style: normal;
        font-weight: 700;
        line-height: 100%;
        padding-bottom: 20px;
    }
    .item__text p {
        max-width: 428px;
        color: var(--second, #636F87);
        font-size: 14px;
        font-family: Plus Jakarta Sans;
        font-style: normal;
        font-weight: 400;
        line-height: 140%;
    }
}