@media screen and (max-width: 600px) {
    .registor__block {
        padding-bottom: 50px;
        text-align: center;
    }
    .register__tilte {
        display: inline-block;
        max-width: 650px;
        color: #101010;
        text-align: center;
        font-size: 28px;
        font-family: Plus Jakarta Sans;
        font-style: normal;
        font-weight: 700;
        line-height: 120%;
        padding: 16px;
    }
    .register__subtitle {
        display: inline-block;
        color: #636F87;
        text-align: center;
        font-size: 14px;
        font-family: Plus Jakarta Sans;
        font-style: normal;
        font-weight: 400;
        line-height: 140%;
        padding-bottom: 40px;
    }
    .form__block {
        max-width: 100%;
        border-radius: 6px;
        background: #F2F4F6;
        padding: 44px 0;
    }
    
    .register__form input {
        width: 85%;
    }
    .register__subtitle2 {
        color: #858EA1;
        text-align: center;
        font-size: 12px;
        font-family: Plus Jakarta Sans;
        font-style: normal;
        font-weight: 400;
        line-height: 130%;
    }
    
}