@media screen and (max-width: 1120px) {
    .footer__block {
        padding: 70px 60px;
    }
    .footer-left__list2{
        display: none;
    }
    
}

@media screen and (max-width: 766px) {
    .footer__block {
        display:block;
        padding: 30px 20px;
    }
    .footer-left__list1{
        display: none;
    }
    .right__item {
        color: var(--second-2, #A1A8B7);
        font-size: 17px;
        font-family: Plus Jakarta Sans;
        font-style: normal;
        font-weight: 300;
        line-height: 150%;
        padding-bottom:20px ;
        cursor: pointer;
    }
    .icons__items + .icons__items{
        margin-left: 25px;
    }
}