@media screen and (max-width:832px) {
    .card {
        padding: 0px;
    }
    .card__list {
        flex-wrap: wrap;
        align-items: center;
        gap: 30px;
        justify-content: center;
    }
    .card__item {
        max-width: 100%;
        max-height: 358px;
        padding: 30px 20px;
        flex-shrink: 0;
        border-radius: 8px;
        border: 2px solid #E1E7ED;
    }
    .card__title {
        color: #101010;
        text-align: center;
        font-size: 28px;
        font-family: Plus Jakarta Sans;
        font-style: normal;
        font-weight: 600;
        line-height: 100%;
        padding-bottom: 30px;
    }

    .card__item h5 {
        font-size: 28px;
        font-family: Plus Jakarta Sans;
        font-style: normal;
        font-weight: 600;
        line-height: 100%;
        padding-bottom: 10px;
    }
    
    .card__item p {
        font-size: 15px;
        font-family: Plus Jakarta Sans;
        font-style: normal;
        font-weight: 400;
        line-height: 130%;
        padding-bottom: 25px;
    }
    
    .card__item span {
        font-size: 16px;
        font-family: Plus Jakarta Sans;
        font-style: normal;
        font-weight: 400;
        cursor: pointer;
        line-height: 30%;
        font-weight: bold;
    }
    
}