
.header__block {
    padding: 30px 0px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    background: #FFF;
    box-shadow: 0 0 19  silver;
}

.img__block {
    display: flex;
    align-items: center;
    gap: 50px;
}

.header__list {
    display: flex;
    align-items: center;
    gap: 30px;
}

.header__list li a {
    color: var(--main-text, #101010);
    font-size: 16px;
    font-family: Plus Jakarta Sans;
    font-style: normal;
    font-weight: 500;
    line-height: 150%;
}

.header__btn {
    padding: 12px 24px;
    border: none;
    border-radius: 6px;
    background: #101010;
    cursor: pointer;
    color: #fff;
}



.header__list li a:hover {
    transition: .3 all;
    color: #1AAB7B;
}


.header__btn:hover {
    transition: .3s ease;
    background-color: #fff;
    color: #1AAB7B;
    border: 1px solid #1AAB7B;
}

.burger__block{
    display: none;
}