.registor__block {
    padding-bottom: 200px;
    text-align: center;
}

.register__tilte {
    display: inline-block;
    max-width: 850px;
    color: #101010;
    text-align: center;
    font-size: 36px;
    font-family: Plus Jakarta Sans;
    font-style: normal;
    font-weight: 700;
    line-height: 140%;
    padding: 16px;
}

.register__subtitle {
    display: inline-block;
    max-width: 650px;
    color: #636F87;
    text-align: center;
    font-size: 18px;
    font-family: Plus Jakarta Sans;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    padding-bottom: 60px;
}

.form__block {
    max-width: 100%;
    border-radius: 6px;
    background: #F2F4F6;
    padding: 64px 0;
}

.register__form input {
    width: 60%;
    gap: 40px;
}

.register__form input {
    padding: 15px;
    margin-bottom: 50px;
    border: none;
    background-color: transparent;
    border-bottom: 1px solid #636F87;
}

.register__btn {
    border: none;
    border-radius: 6px;
    background: #1AAB7B;
    cursor: pointer;
    color: #fff;
    padding: 15px 45px;
    margin-bottom: 24px;
}

.register__btn:hover {
    transition: .4s all;
    border: 1px solid #1AAB7B;
    background-color: #fff;
    color: #1AAB7B;
}

.register__subtitle2 {
    color: #858EA1;
    text-align: center;
    font-size: 16px;
    font-family: Plus Jakarta Sans;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
}


