.hero__block {
    display: flex;
    /* align-items: center; */
    justify-content: space-between;
    padding-top: 90px;
    padding-bottom: 106px;
}

.hero-left__block {
    padding-right: 74px;
}

.hero-left__block h1 {
    color: #101010;
    font-size: 56px;
    font-style: normal;
    font-weight: 700;
    line-height: 140%;
    padding-bottom: 20px;
}

.hero-left__block p {
    color: #636F87;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    padding-bottom: 40px;
}

.hero-left__block button {
    padding: 15px 35px;
    border-radius: 6px;
    background: #1AAB7B;
    border: none;
    color: #fff;
    cursor: pointer;
}

.hero-left__block button:hover {
    border: 1px solid #1AAB7B;
    padding: 14px 34px;
    background-color: #fff;
    color: #1AAB7B;
    transition: .3s all;
}

.hero-right__block iframe {
    width: 592px;
    height: 334px;
    flex-shrink: 0;
    border-radius: 6px;
}