.footer__block {
    padding: 100px 112px;
    background: #101010;
    border-bottom: 1px solid #3B4155;
    display: flex;
    justify-content: space-between;
}

.footer__left {
    display: flex;
    gap: 151px;
}

.footer-left__list1 li:nth-child(1) {
    color: var(--white, #FFF);
    font-size: 20px;
    font-family: Plus Jakarta Sans;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
}

.footer-left__list1 li {
    color: var(--second-2, #A1A8B7);
    font-size: 18px;
    font-family: Plus Jakarta Sans;
    font-style: normal;
    font-weight: 300;
    line-height: 150%;
    padding-bottom: 30px;
    cursor: pointer;
}

.footer-left__list1 li:nth-child(6) {
    padding-bottom: 0px;
}


.footer-left__list2 li {
    color: var(--second-2, #A1A8B7);
    font-size: 18px;
    font-family: Plus Jakarta Sans;
    font-style: normal;
    font-weight: 300;
    line-height: 150%;
    padding-bottom: 30px;
    cursor: pointer;

}

.footer-left__list2 li:nth-child(1) {
    color: var(--white, #FFF);
    /* Bold/Headline 6 */
    font-size: 20px;
    font-family: Plus Jakarta Sans;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
}

.footer-left__list2 li:nth-child(4) {
    padding-bottom: 0px;
}


.right__item {
    color: var(--second-2, #A1A8B7);
    font-size: 18px;
    font-family: Plus Jakarta Sans;
    font-style: normal;
    font-weight: 300;
    line-height: 150%;
    padding-bottom:26px ;
    cursor: pointer;
}

.right__item:nth-child(1) {
    color: var(--white, #FFF);
    font-size: 20px;
    font-family: Plus Jakarta Sans;
    font-style: normal;
    font-weight: 700;
    line-height: normal;
}

.icons__lists{
    display: flex;
    align-items: center;
}
.icons__items + .icons__items{
    margin-left: 40px;
}
.icons__items{
    cursor: pointer;
}