.card {
    padding: 50px 0;
}

.card__title {
    color: #101010;
    text-align: center;
    font-size: 36px;
    font-family: Plus Jakarta Sans;
    font-style: normal;
    font-weight: 600;
    line-height: 140%;
    padding-bottom: 40px;
}

.card__item {
    max-width: 384px;
    max-height: 358px;
    padding: 40px 30px;
    flex-shrink: 0;
    border-radius: 6px;
    border: 2px solid #E1E7ED;
}

.card__list {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: space-between;
}

.card__item h5 {
    font-size: 32px;
    font-family: Plus Jakarta Sans;
    font-style: normal;
    font-weight: 600;
    line-height: 140%;
    padding-bottom: 10px;
}

.card__item p {
    font-size: 17px;
    font-family: Plus Jakarta Sans;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    padding-bottom: 40px;
}

.card__item span {
    font-size: 18px;
    font-family: Plus Jakarta Sans;
    font-style: normal;
    font-weight: 400;
    cursor: pointer;
    line-height: 40%;
    font-weight: bold;
}

.card__item:hover {
    border-radius: 6px;
    background: #1AAB7B;
    color: #fff;
    transition: .4s ease;
}

.card__item span:hover {
    text-decoration-line: underline;
    transition: .3s all;
}