.bussines {
    padding: 100px 0 150px 0;
}

.bussines-site__title {
    text-align: center;
}

.bussines__title {
    color: #101010;
    text-align: center;
    font-size: 36px;
    font-family: Plus Jakarta Sans;
    font-style: normal;
    font-weight: 600;
    line-height: 140%;
    padding-bottom: 16px;
}

.bussines__subtitle {
    display: inline-block;
    max-width: 650px;
    color: #636F87;
    text-align: center;
    font-size: 18px;
    font-family: Plus Jakarta Sans;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
    padding-bottom: 90px;
}

.bussines__item {
    display: flex;
    align-items: center;
}

.item__text {
    padding-left: 32px;
}

.item__text h4 {
    display: block;
    color: var(--main-text, #101010);
    font-size: 40px;
    font-family: Plus Jakarta Sans;
    font-style: normal;
    font-weight: 700;
    line-height: 140%;
    padding-bottom: 20px;
}

.item__text p {
    max-width: 488px;
    color: var(--second, #636F87);
    font-size: 18px;
    font-family: Plus Jakarta Sans;
    font-style: normal;
    font-weight: 400;
    line-height: 150%;
}

.bussines__item:nth-child(1){
    padding-bottom: 100px;
    gap: 50px;
}
.bussines__item:nth-child(2){
    padding-bottom: 100px;
    gap: 82px;
}
.bussines__item:nth-child(3){
    gap: 82px;
}