@media screen and (max-width: 1220px) {
    .hero__block {
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        padding-top: 40px;
        padding-bottom: 70px;
    }

    .hero-left__block {
        text-align: center;
        padding-bottom: 40px;
    }
}

@media screen and (max-width: 726px) {
    .hero__block {
        flex-wrap: wrap;
        align-items: center;
        justify-content: center;
        padding-top: 40px;
        padding-bottom: 70px;
    }

    .hero-left__block {
        text-align: center;
        padding: 0;
        padding-bottom: 40px;

    }

    .hero-left__block h1 {
        color: #101010;
        font-size: 36px;
        font-style: normal;
        font-weight: 700;
        line-height: 100%;
        padding-bottom: 20px;
    }

    .hero-left__block p {
        color: #636F87;
        font-size: 14px;
        font-style: normal;
        font-weight: 400;
        line-height: 100%;
        padding-bottom: 20px;
    }

    .hero-right__block iframe {
        max-width: 400px;
        height: 250px;
    }
}
@media screen and (max-width: 445px) {
    .hero-right__block iframe {
        max-width: 340px;
        height: 200px;
    }
}